.l-section-banners {
  margin-bottom: 1.875rem;
  overflow: hidden;
  position: relative; }
  .l-section-banners__items {
    position: relative;
    z-index: 3;
    display: flex;
    flex-wrap: wrap; }
  .l-section-banners__bg {
    position: absolute;
    height: 100%;
    width: 100%;
    left: 0;
    top: 0;
    z-index: 1; }
  .l-section-banners__preload {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    display: none;
    background: transparent;
    z-index: 2; }
    .l-section-banners__preload .spinner {
      z-index: 2; }
  .l-section-banners__container {
    position: relative;
    z-index: 3; }
  .l-section-banners.is-wait-loading .l-section-banners__container,
  .l-section-banners.is-wait-loading .l-section-banners__bg {
    display: none; }
  .l-section-banners.is-wait-loading .l-section-banners__preload {
    display: block; }
  .l-section-banners.has-bg {
    background-color: #f2f2f2;
    padding: 1.875rem 0; }
  .l-section-banners--view1 {
    min-height: 226px; }
    .l-section-banners--view1.has-bg {
      min-height: 285px; }
  .l-section-banners--slider {
    min-height: 316px; }

.b-banner-item {
  background: #fff;
  margin-bottom: 1px;
  position: relative;
  background: #000; }
  .b-banner-item__bg {
    position: absolute;
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: center top; }
  .b-banner-item__link {
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 2; }
  .b-banner-item--view1 {
    height: 225px;
    width: 33.2%;
    width: calc(33.3334% - 1px);
    margin-right: 1px; }
    .b-banner-item--view1:nth-child(3n) {
      width: calc(33.337% - 0.5px);
      margin-right: 0; }
  .b-banner-item--slider {
    height: 315px;
    width: 100%; }
  .b-banner-item__overlay {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    padding: 0.625rem;
    overflow: hidden; }
  .b-banner-item__overlay:before {
    content: "";
    position: absolute;
    z-index: 1;
    left: 0;
    right: 0;
    bottom: 0;
    height: 65%;
    transition: opacity 0.2s;
    background-image: linear-gradient(to bottom, transparent, rgba(0, 0, 0, 0.75));
    backface-visibility: hidden; }
  .b-banner-item:hover .b-banner-item__overlay:before {
    opacity: 0.9; }
  .b-banner-item__content {
    position: absolute;
    bottom: 0;
    z-index: 3;
    padding-right: 1.875rem;
    padding-bottom: 0.625rem;
    pointer-events: none;
    transition: bottom 0.3s; }
  .b-banner-item__title {
    color: #f8f9fa;
    white-space: normal;
    display: block;
    line-height: 1.4;
    font-size: 1rem;
    margin-bottom: 0;
    pointer-events: auto; }
  .b-banner-item__date,
  .b-banner-item__title a {
    color: #f8f9fa; }
  .b-banner-item__title a:hover {
    text-decoration: none;
    color: #f8f9fa; }
  .b-banner-item__date {
    margin-bottom: 0.3125rem;
    font-size: 80%; }
  .b-banner-item__desc {
    color: #f8f9fa;
    max-height: 0;
    line-height: 1.125rem;
    overflow: hidden;
    opacity: 0;
    transition: opacity 0.4s ease-out, max-height 0.4s ease-out, margin 0.4s;
    font-size: 80%;
    backface-visibility: hidden; }
  @media (max-width: 991.98px) {
    .b-banner-item__title {
      display: -webkit-box;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;
      overflow: hidden;
      text-overflow: ellipsis;
      max-height: 2.8em;
      font-size: 1rem; } }
  @media (min-width: 768px) {
    .b-banner-item__overlay {
      padding: 1.25rem; }
    .b-banner-item__content {
      padding-bottom: 1rem; } }
  @media (min-width: 992px) {
    .b-banner-item:hover .b-banner-item__desc {
      display: block;
      opacity: 1;
      max-height: 5.9rem;
      margin-top: 0.3125rem;
      transition: opacity 0.4s ease-out, max-height 0.9s ease-out; } }
  @media (min-width: 992px) {
    .b-banner-item__title {
      font-size: 1.25rem;
      line-height: 1.2; } }

@media (max-width: 991.98px) {
  .l-section-banners--slider {
    min-height: 280px; }
  .b-banner-item--slider {
    height: 280px; } }

@media (max-width: 767.98px) {
  .l-section-banners--slider {
    min-height: 220px; }
  .b-banner-item--view1 {
    height: 145px; }
    .b-banner-item--view1, .b-banner-item--view1:nth-child(3n) {
      width: 49.5%;
      width: calc(50% - 1px); }
    .b-banner-item--view1:nth-last-child(1):nth-child(odd) {
      width: 100%; }
  .b-banner-item--slider {
    height: 220px; } }
